import React from 'react'
import { Link } from 'react-scroll'
import { Navbar, Nav } from 'react-bootstrap'

const Header = () => (
  <Navbar className="sticky-top m-0 header">
    <h3>Charles Curtis</h3>
    <Nav className="ml-auto text-center">
      <Link
        to='home'
        spy={true}
        smooth={true}
        offset={-120}
        duration={500}
        className="scroll-nav"
        style={{ textDecoration: 'none' }}
      >Home</Link>
      <Link
        to='projects'
        spy={true}
        smooth={true}
        offset={-120}
        duration={500}
        className="scroll-nav"
        style={{ textDecoration: 'none' }}
      >Projects</Link>
      <Link
        to='about'
        spy={true}
        smooth={true}
        offset={-120}
        duration={500}
        className="scroll-nav"
        style={{ textDecoration: 'none' }}
      >About</Link>
      <Link
        to='resume'
        spy={true}
        smooth={true}
        offset={-120}
        duration={500}
        className="scroll-nav"
        style={{ textDecoration: 'none' }}
      >Resume</Link>
      <Link
        to='contact'
        spy={true}
        smooth={true}
        offset={-120}
        duration={500}
        className="scroll-nav"
        style={{ textDecoration: 'none' }}
      >Contact</Link>
    </Nav>
  </Navbar>
)

export default Header



